// ** React Imports
import { Fragment, useEffect, useState } from 'react'

// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Form,
  Badge,
  Label,
  Input,
  Button,
  CardBody,
  FormFeedback,
  Modal,
  ModalHeader,
  ModalBody,
  Table,
  CardHeader
} from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import { addExemption, updateExemption, deleteExemption } from './store/exemptionreason'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Third Party Components
import classnames from 'classnames'
import { useDispatch } from 'react-redux'
import toast from 'react-hot-toast'
import { Edit, Trash, Plus } from 'react-feather'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

const InvoiceAccounts = () => {
  const MySwal = withReactContent(Swal)
  const [data, setData] = useState([])
  const [selected, setSelected] = useState(null)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const [formModal, setFormModal] = useState(false)
  const dispatch = useDispatch()

  const schema = yup.object().shape({
    organizationId: yup.string().default(activeOrgId),
    name: yup.string().required('Please Enter Exemption'),
    updatedBy: yup.string().default(userId),
    createdBy: yup.string().default(userId)
  })

  const { handleSubmit, formState: { errors }, control, reset } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })


  const getList = () => {
    axios.post('/exemptionreasons/list')
      .then((res) => {
        setData(res.data.exemptionreasons)
      })
      .catch((err) => { console.log(err) })
  }


  const deletefn = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteExemption(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Account has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        getList()
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const closeModal = () => {

    setFormModal(false)
    reset({
      organizationId: activeOrgId,
      id: '',
      name: ''
    })
  }

  const onSubmit = async data => {
    setBlock(true)
    let eventname
    if (selected !== null) {
      await dispatch(updateExemption(data))
      reset({ name: '' })
      eventname = 'Added'
      setSelected(null)
    } else {
      await dispatch(addExemption(data))
      eventname = 'Updated'
      reset({ name: '' })
    }
    closeModal()
    setBlock(false)
    toast.success(`Exemption Reason ${eventname} Succesfully`, { position: 'bottom-right' })
    getList()
  }

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  useEffect(async () => {
    getList()
  }, [])

  const openEditModal = card => {
    setSelected(card)
    setFormModal(true)
    reset({
      organizationId: activeOrgId,
      id: card.id,
      name: card.name
    })
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardHeader>
            <h5>Exemption Reason</h5>
            <Button color='primary' size='sm' onClick={() => { setFormModal(true) }}> <Plus size={15} />Add Reason</Button>
          </CardHeader>
          <CardBody className='py-25'>
            <Row className='gx-4'>
              <Col lg='12' className='mt-2 mt-lg-0'>
                <Table className='table table-hover table-bordered'>
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Status</th>
                      <th>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {data.map((card, index) => {
                      return (
                        <tr key={index}>
                          <td className='mb-0'>{card.name}</td>
                          <td>
                            {card.status === 1 ? (
                              <Badge color='light-success' className='ms-50'>
                                Active
                              </Badge>
                            ) : (
                              <Badge color='light-danger' className='ms-50'>
                                InActive
                              </Badge>
                            )}
                          </td>
                          <td className=''>
                            {card.organizationid !== null &&
                              <div className='d-flex'>
                                <Edit size={15} className='me-75' onClick={() => openEditModal(card)}>
                                  Edit
                                </Edit>
                                <Trash size={15} onClick={() => { deletefn(card.id) }}>Delete</Trash>
                              </div>
                            }
                          </td>
                        </tr>
                      )
                    })}
                  </tbody>
                </Table>
              </Col>

              <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
                <ModalHeader toggle={() => closeModal()}>Exemption Reason</ModalHeader>
                <ModalBody>
                  <Row lg='12'>
                    {
                      Object.keys(errors).map((key) => {

                        return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

                      })
                    }
                    <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>

                      {getRow('Exemption Reason', 'name')}

                      <Col className='mt-2 pt-1' xs={12}>
                        <Button type='submit' className='me-1' color='primary'>
                          Submit
                        </Button>
                        <Button type='button' className='me-1' color='warning' onClick={() => closeModal()}>
                          Cancel
                        </Button>
                      </Col>

                    </Row>
                  </Row>
                </ModalBody>
              </Modal>
            </Row>
          </CardBody>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default InvoiceAccounts
